import localforage from 'localforage'

localforage.config({
  storeName: 'MorPhoto', // Should be alphanumeric, with underscores.
  description: 'image blobs'
})

export const imgStore = localforage.createInstance({
  name: 'imgStore'
})
export const apolloStore = localforage.createInstance({
  name: 'apolloStore'
})
export const reduxStore = localforage.createInstance({
  name: 'reduxStore'
})
